          <el-radio class="class" :label="2">1.</el-radio>
          <el-radio :label="1">2.</el-radio>
          <el-radio :label="0">3.</el-radio>
        </el-radio-group>
      </div>
      <div class="btn">
        <el-button @click="commit" type="primary"></el-button>
        <el-button @click="back" type="primary"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'testtopic',
  data () {
    return {
      data: [{
