        problem: '',
      },{
        id: 28,
        radio: '',
        problem: '',
      },{
        id: 29,
        radio: '',
        problem: '',
      },{
        id: 30,
        radio: '',
        problem: '',
      }]
    }
  },
  creted() {
    console.log(this.$route.params);
    
  },
  methods: {
    back() {
      this.$router.go(-1);//
    },
    change1(val) {
      console.log(val);
    },
    change2() {

    },
    commit() {
      var name = ""
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      if (month < 10) {
          month = "0" + month;
      }
      if (day < 10) {
          day = "0" + day;
      }
      var nowDate = year + "-" + month + "-" + day;
      var describe = '';
      var count = 0;
      var state = 1;
      this.data.forEach((val) => {
        if(val.radio.length !== 0 ){
          count+= val.radio
        }else{
          state = 0;
        }
      })
      if( state === 1){
        if( count < 6 ){
          describe = ""
        }else if( count > 5 && count < 14 ){
          describe = ""
        }else if( count > 13 && count < 26 ){
          describe = ""
        }else if( count > 25 ){
          describe = "."          
        }
        console.log(count,describe);
        this.$http.post('api/files/savehealthrecord',{file_name:name,score: count,describe: describe,date: nowDate,type: 4}
        ).then(res => {
            console.log(res);
            if(res.data.code === 200){
               this.$message({
                message:'',
                type: 'success',
                duration: 2000});
                setTimeout(() => {
                  this.$router.push({ name: 'hearttest'})
                },1000)
            }
        }).catch(err => {
            console.log(err)
        })
      }else{
        this.$message({
          message:'',
          type: 'warning',
          duration: 2000});
      }
      
    }
  }
}
