import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Articleinfo from '@/components/Articleinfo'
import Videoinfo from '@/components/Videoinfo'
import Exercise from '@/components/Exercise'
import Advertising from '@/components/Advertising'
import About from '@/components/About'
import Hearttest from '@/components/Hearttest'
import Heartlayout from '@/components/Heartlayout'
import { Testtopic1 } from '@/components/meter'
import { Testtopic2 } from '@/components/meter'
import { Testtopic3 } from '@/components/meter'
import { Testtopic4 } from '@/components/meter'
import { Testtopic5 } from '@/components/meter'
import { Testtopic6 } from '@/components/meter'
import { Testtopic7 } from '@/components/meter'
import { Testtopic8 } from '@/components/meter'
import { Testtopic9 } from '@/components/meter'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/articleinfo',
      name: 'articleinfo',
      component: Articleinfo
    },
    {
      path: '/videoinfo',
      name: 'videoinfo',
      component: Videoinfo
    },
    {
      path: '/exercise',
      name: 'exercise',
      component: Exercise
    },
    {
      path: '/advertising',
      name: 'advertising',
      component: Advertising
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/heartlayout',
      component: Heartlayout,
      name: 'heartlayout',
      redirect: '/hearttest/heartlayout',
      children: [
        {
          path: '/hearttest/heartlayout',
          name: 'hearttest',
          component: Hearttest,
          meta: {
            title:"",
          }
        },
        {
          path: '/hearttest/testtopic1',
          name: 'testtopic1',
          component: Testtopic1,
          meta: {
            title: 'Beck',
          }
        },
        {
          path: '/hearttest/testtopic2',
          name: 'testtopic2',
          component: Testtopic2,
          meta: {
            title:"",
          }
        },
        {
          path: '/hearttest/testtopic3',
          name: 'testtopic3',
          component: Testtopic3,
          meta: {
            title:"",
          }
        },
        {
          path: '/hearttest/testtopic4',
          name: 'testtopic4',
          component: Testtopic4,
          meta: {
            title:"",
          }
        },
        {
          path: '/hearttest/testtopic5',
          name: 'testtopic5',
          component: Testtopic5,
          meta: {
            title:"",
          }
        },
        {
          path: '/hearttest/testtopic6',
          name: 'testtopic6',
          component: Testtopic6,
          meta: {
            title:"CESD",
          }
        },
        {
          path: '/hearttest/testtopic7',
          name: 'testtopic7',
          component: Testtopic7,
          meta: {
            title:"EAP",
          }
        },
        {
          path: '/hearttest/testtopic8',
          name: 'testtopic8',
          component: Testtopic8,
          meta: {
            title:"",
          }
        },
        {
          path: '/hearttest/testtopic9',
          name: 'testtopic9',
          component: Testtopic9,
          meta: {
            title:"",
          }
        }
      ]
    },
  ]
})



