/**
 * axios
 * 
 */
import Vue from 'vue'
import axios from 'axios'
// import VueAxios from 'vue-axios'
import router from '../router'
// import store from '../store'
const qs = require('qs')
// 
if (process.env.NODE_ENV === 'development') {
  axios.defaults.baseURL = 'api'
} else if (process.env.NODE_ENV === 'production') {
  axios.defaults.baseURL = 'https://shanyun.chunyingkeji.com'
}

// 
axios.defaults.timeout = 10000

// post
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'

axios.defaults.headers.get['Content-Type'] = 'application/json;charset=UTF-8'
// axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'

// 
axios.interceptors.request.use(
  config => {
    // tokenhttpheadertoken
    // tokentoken
    // var token = sessionStorage.getItem("token");
    // if (token) {  // tokenhttpheadertoken
    //   config.headers['admin-token'] = token;
    // }
    var token = sessionStorage.getItem("apptoken");
    config.headers['app-token'] = token;
    config.headers['app-type'] = 2;
  return config;
  },
  error => {
    return Promise.error(error)
  }
)

// 
axios.interceptors.response.use(
  response => {
    if (response.status === 200) {
      return Promise.resolve(response)
    } else {
      return Promise.reject(response)
    }
  },
  // 200
  error => {
    if (error.response.status) {
      switch (error.response.status) {
        case 400:
          console.log('400')
          break
        // 401: 
        // 
        // 
        case 401:
          router.replace({
            path: '/login',
            query: { redirect: router.currentRoute.fullPath }
          })
          break
        // 403 token
        // 
        // tokenvuextoken
        // 
        case 403:
          console.log('')
          // token
          localStorage.removeItem('token')
          // store.commit('loginSuccess', null)
          // fullPath
          setTimeout(() => {
            router.replace({
              path: '/login',
              query: {
                redirect: router.currentRoute.fullPath
              }
            })
          }, 1000)
          break
        // 404
        case 404:
          console.log('')
          break
        // 
        default:
          console.log('500')
      }
      return Promise.reject(error.response)
    }
  }
)
/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params: params
      })
      .then(res => {
        resolve(res.data)
      })
      .catch(err => {
        reject(err.data)
      })
  })
}
/**
 * postpost
 * @param {String} url [url]
 * @param {Object} params []
 */
export function post(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .post(url, params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}



